.divTitulo {
    background-color: white;
    display: flex;
    justify-content: center;
}

.divMarkdownTexto {
    display: flex;
    justify-content: center;
}

.divMarkdown {
    position: fixed;
    bottom: 0;
    left: 50%;
    right: 0;
    top: 5%;
    margin: 5px;
    font-size: 18px;
}

#preview {
    width: 100%;
    max-height: 100%;
    word-wrap: break-word;
    overflow-y: auto;
}

.divTexto {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 50%;
    top: 5%;
    margin: 5px;
}

#editor {
    width: 100%;
    height: 100%;
    resize: none;
    outline: none;
    border: none;
    opacity: .7;
    font-size: 18px;
}

tr {
    border-top: 1px solid #c6cbd1;
    background: #fff;
  }
  
  th,
  td {
    padding: 6px 13px;
    border: 1px solid #dfe2e5;
  }
  
  table tr:nth-child(2n) {
    background: #f6f8fa;
  }